import {sendContactForm} from '../fetch-request'

export default {
  name: "MContact",
  data(){
    return {
      email: "",
      msg : "",
      statusForm : ""
    }
  },
  methods: {

    sendData(){
      const body = {
        contact: {
          email : this.email,
          msg : this.msg,
        }
      }

      if(this.checkInputs()){

        sendContactForm(body).then((res)=>{
          if(res.error){
          this.printMsg("Error al enviar")
          return}
          this.printMsg("Enviado")
          this.clearInputs()
        }).catch()

      }
    },
    checkInputs(){
      if(this.isMSG()  &&  this.isEmail()){return true}
      else{
        if(!this.isMSG()){this.printMsg("El mensaje es muy corto")}
        if(!this.isEmail()){this.printMsg("El email no es valido")}
        return false
      }
    },
    isMSG(){
      if(this.msg.length > 5){return true}
      else{return false}
    },
    isEmail(){
        if(!this.email.includes('@')  || !this.email.includes('.')){return false}
        else{return true}
    },
    printMsg(text){
      this.statusForm = text;
      setTimeout(()=>{this.statusForm = ''}, 2500)
    },
    clearInputs(){
      this.msg = ''
      this.email = ''
    }
  }
}
